.playbook-list {
  display: flex;
  flex-flow: row wrap;
  overflow-y: scroll;
  height: 80vh;

  .playbook-card {
    min-width: 300px;
    max-width: 350px;
    width: 30%;
    display: flex;
    flex-grow: 1;
    margin: 10px;
    padding: 10px;
    border-radius: 15px;
    min-height: 20vh;
    max-height: 30vh;
    height: fit-content;
    &:hover{
      box-shadow: #dfdfff 0px 0px 15px;
    }

    .playbook-card-title {
      text-align: justify;
      background: #ffefef;
      font-size: 1.3rem;
      border-radius: 15px;
      padding: 0 10px;
      border: 1px solid #ffdfdf;
    }

    .playbook-card-text {
      font-size: 0.8rem;
      padding: 0 10px;
      flex-grow: 1;
      color: #706666;
      font-style: italic;
    }

    .playbook-card-description {
      text-align: justify;
      font-size: 0.9rem;
      padding: 5px 10px;
    }

    .playbook-card-delete {
      display: flex;
      justify-content: flex-end;
      padding: 10px;
    }
  }
}


@media (max-width: 576px) {
  .playbook-list {
    .playbook-card {
      width: 95%;
      max-width: unset;
    }
  }
}

