body {
  @import "task";
  @import "playbook-list";
  .right{
    float:right;
  }

  .link-icon{
    &.delete{
      &:hover{
        color:red;
      }
    }
    &.dismiss{
      &:hover{
        color:brown;
      }
    }
    &.edit{
      &:hover{
        color:blue;
      }
    }
    &.save{
      &:hover{
        color:green;
      }
    }
  }

  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: #FFFFFF;

  .signin-form {
    width: 50vw;
    margin: auto;
  }

  .container {
    width: 100vw;
  }

  .page-wrapper {
    display: flex;
    flex-direction: row;

    .left-panel {
      z-index: 1;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    }

    .right-panel {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      z-index: 0;
      .main-block{
        padding-top: 10px;
      }
    }
  }

  .cursor-pointer {
    cursor: pointer;
  }

  .active-task {
    width: 100%;

    &.title {
      text-align: center;
      font-size: 1.5rem;
    }

    &.playbook-title {
      text-align: center;
      color: #3e4551;
      font-size: 0.75rem;
    }

    &.active-timer {
      text-align: center;
      font-size: 2rem;
    }
  }

  .task-title {
    font-size: 1rem;

  }

  .new-task-time-input {
    width: 2rem;
  }

  .playbook-col {
    height: 70vh;
    overflow-y: scroll;
  }

  #app-version {
    position: absolute;
    bottom: 0;
    text-align: center;
    margin: auto;
    width: 100%;
    left: 0;
    color: #bbbbbb
  }
}

a {
  color: inherit;
}

.list-group-flush {
  color: #495057;
}

.active {
  background-color: #e9ecef !important;
}

main {
  background-color: #EDEDEE;
}

.navbar-brand {
  margin-left: 15px;
  color: #2196f3 !important;
}

.flexible-navbar {
  margin-bottom: 10px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.sidebar-fixed {
  left: 0;
  top: 0;
  height: 100vh;
  width: 270px;
  background-color: #fff;
  padding: 1.5rem;
  padding-top: 0;
  margin-bottom: 10px;
}

.flexible-navbar {
  transition: padding-left 0.5s;
  background: #fff;
}

.sidebar-fixed .logo-wrapper img {
  width: 100%;
  padding: 2.5rem;
}

.list-group-item {
  display: block !important;
  transition: background-color 0.3s;

  &:hover {
    color: #49505B;
    text-decoration: none;
    background-color: #f8f9fa;
  }
}

.list-group .active {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  border-radius: 5px;
}

.card-title {
  font-size: 1.5rem;
  font-weight: 600;
}

.card-subtitle {
  font-size: 1.25rem;
  font-weight: 600;
}

.full {
  height: 70vh;
}

.bad-gateway-row {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 55%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.card {
  .view.gradient-card-header {
    padding: 1rem 1rem;
    text-align: center;
  }

  h3, &.card-cascade h4 {
    margin-bottom: 0;
  }
  .card-body{
    textarea, input{
       padding: 5px;
    }
  }
  .card-footer{
    display: flex;
    button{
      flex-grow: 1;
      padding: 10px;
      flex-basis:  0;
    }
  }
}

.cascading-admin-card .admin-up {
  margin-left: 4%;
  margin-right: 4%;
  margin-top: -20px;

  .fa {
    box-shadow: 0 2px 9px 0 rgba(0, 0, 0, 0.2), 0 2px 13px 0 rgba(0, 0, 0, 0.19);
    padding: 1.7rem;
    font-size: 2rem;
    color: #fff;
    text-align: left;
    margin-right: 1rem;
    border-radius: 3px;
  }

  .data {
    float: right;
    margin-top: 2rem;
    text-align: right;
  }
}

.admin-up .data p {
  color: #999999;
  font-size: 12px;
}

.classic-admin-card .card-body {
  color: #fff;
  margin-bottom: 0;
  padding: 0.9rem;

  p {
    font-size: 13px;
    opacity: 0.7;
    margin-bottom: 0;
  }

  h4 {
    margin-top: 10px;
  }
}

.form-inline {
  flex-flow: unset;
}

.breadcrumb {
  margin: 0;
}

.activeClass .list-group-item {
  color: #fff;
  border-color: #007bff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  border-radius: 5px !important;
  background-color: #007bff !important;
}


@media (max-width: 1199.98px) {
  .hide-on-small {
    display: none;
  }
  .flexible-content {
    padding-left: 0;
  }
  .flexible-navbar {
    padding-left: 10px;
  }
  #content, .page-footer {
    margin-left: 0;
  }
  .card.cascading-admin-card {
    margin-top: 2.5rem;
  }
}

@media (max-width: 576px) {
  .card.cascading-admin-card {
    margin-top: 2.5rem;
  }
  #breadcrumb {
    flex-direction: column;
  }
}
